@import url(https://fonts.googleapis.com/css?family=Lato|Open+Sans&display=swap);
* {
  margin: 0;
  padding: 0; }

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.body {
  font-family: 'Open Sans', sans-serif;
  background-color: var(--color-body); }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

:root {
  /* --color-primary: #2a2e45;
  --color-accent: #1B998B; */
  /* --color-accent: #EF5B5B; */
  /* --color-accent: #9B5094; */
  --color-nav: #2a2e45;
  --color-nav-variant: #505466;
  --color-primary: #EF5B5B;
  --color-secondary: #1B998B;
  --color-accent: #9B5094;
  --color-darkgrey: darkgrey;
  --color-grey: grey;
  --color-white: white;
  /* --color-body: #FCFCFC; */
  /* --color-primary: #fff; */ }

.App {
  width: 100%;
  margin: auto; }

.App .body .content {
  width: 100%;
  margin: auto; }

@media (min-width: 768px) {
  .App .body .content .boxed {
    width: 60%;
    margin: auto; } }

@media (max-width: 767px) {
  .App .body .content .boxed {
    padding-left: 20px;
    padding-right: 20px; } }

.App .body .content .boxed {
  min-height: 100vh;
  padding-top: 100px;
  padding-bottom: 40px; }

.footer {
  padding: 5px;
  background-color: #9EA0AA;
  color: white;
  text-align: right; }

button .material-icons {
  vertical-align: bottom;
  font-size: 20px;
  margin-right: 5px; }

button {
  outline: none;
  border-radius: 3px;
  font-size: 1em;
  padding: 0.5em 0.5em;
  background: transparent;
  color: var(--color-darkgrey);
  border: 2px solid var(--color-darkgrey); }

button.primary {
  color: var(--color-primary);
  border: 2px solid var(--color-primary); }

button.secondary {
  color: var(--color-secondary);
  border: 2px solid var(--color-secondary); }

button.accent {
  color: var(--color-accent);
  border: 2px solid var(--color-accent); }

button.block {
  background-color: var(--color-white);
  color: var(--color-white);
  border: none; }

button.block.primary {
  background-color: var(--color-primary); }

button.block.secondary {
  background-color: var(--color-secondary); }

button.block.accent {
  background-color: var(--color-accent); }

button.icon {
  font-size: 0.8em;
  padding: 0em 0em;
  background: transparent;
  border: none; }

.nav {
  font-family: 'Open Sans', sans-serif; }

.navbar {
  background-color: var(--color-nav);
  display: grid;
  grid-template-columns: 1fr 14fr 1fr 3fr;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  height: 70px;
  position: fixed;
  width: 100%;
  /* box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 2px 6px 0 rgba(0, 0, 0, 0.19); */
  box-shadow: inset 0 -1px 0 0 rgba(0, 0, 0, 0.2), 0 1px 5px rgba(0, 0, 0, 0.19); }

.navbar.transparent {
  background-color: transparent; }

.navbar .logo {
  display: grid;
  margin-right: 5vh;
  margin-left: 5vh;
  font-size: 32px;
  font-weight: 600; }

.navbar .rightlogo .logo {
  opacity: 0.4; }

.navbar .logo img {
  width: 100px;
  height: auto; }

.mobilenav .header {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 70px; }

.mobilenav .authheader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px; }

.mobilenav .header .logo {
  display: flex;
  margin-left: -0.25em;
  align-items: center; }

.mobilenav .header .logo2 {
  display: flex;
  align-items: center;
  justify-content: flex-end; }

.mobilenav .header .logo2 img {
  width: 80px;
  height: auto; }

.mobilenav .header .logo img {
  width: 80px;
  height: auto; }

.navbar .rightnav .auth {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 0.75em; }

.navbar .rightnav .auth .material-icons {
  font-size: 2em;
  color: var(--color-primary); }

.navbar a, .mobilenav a {
  text-decoration: none;
  color: #fcfcfc;
  font-size: 80%; }

.navbar .navitem, .mobilenav .navitem {
  margin-right: 10px;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 20px; }

.navbar .leftnav .navitem.active {
  color: white;
  background-color: var(--color-primary) !important;
  padding-top: 50px;
  /* padding-bottom: 2px; */
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 2px 0 rgba(0, 0, 0, 0.19); }

.navbar .leftnav .navitem:hover {
  background-color: var(--color-nav-variant);
  padding-top: 50px; }

.mobilenav .slider {
  display: grid;
  grid-template-rows: 10fr 1fr;
  height: 100vh;
  width: 60vw;
  background-color: white;
  position: fixed;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
  -webkit-transition-property: width;
  transition-property: width;
  -webkit-transition-duration: 0.1s;
          transition-duration: 0.1s;
  -webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
          transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
  z-index: 1; }

.mobilenav .slider.hide {
  width: 0vw; }

.mobilenav .slider.show {
  padding: 0px;
  padding-left: 1em;
  padding-right: 1em; }

.mobilenav .slider .mobilefooter {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 0.5em;
  color: var(--color-grey); }

.mobilenav .hidetext {
  display: none; }

.mobilenav h1 {
  font-size: 3rem;
  font-weight: 200; }

.navbar .mobilemenu .material-icons {
  font-size: 40px; }

@media (min-width: 768px) {
  .mobilenav {
    display: none; }
  .navbar .mobilemenu {
    display: none; } }

@media (max-width: 767px) {
  .navbar .logo {
    margin-left: 2vh; }
  .navbar .logo img {
    width: 80px;
    height: auto; }
  .navbar .navitem {
    display: none; }
  .navbar .rightnav .auth {
    display: none; }
  .mobilenav .authheader .material-icons {
    color: var(--color-primary);
    font-size: 34px;
    margin: 0px; }
  .mobilenav .navitem p .material-icons {
    vertical-align: bottom;
    font-size: 30px;
    margin-right: 10px; }
  .mobilenav .navitem p {
    margin-bottom: -1rem;
    font-size: 1.5rem;
    margin-left: 1rem;
    color: var(--color-nav); }
  .mobilenav .navitem.active p {
    color: var(--color-primary); }
  .mobilenav .navitem .material-icons:hover {
    color: var(--color-primary); }
  .navbar .leftnav {
    display: flex;
    justify-content: flex-end;
    padding-right: 5vw; } }

.home {
  height: 100vh;
  background-size: cover;
  -webkit-animation: slide 60s linear infinite;
          animation: slide 60s linear infinite; }

.home .container {
  padding-top: 70px; }

.home .container .section1, .home .container .section5 {
  height: calc(100vh - 70px);
  background-color: rgba(161, 94, 73, 0.4);
  display: grid;
  align-content: center;
  justify-content: right;
  text-align: right;
  padding-right: 50px;
  font-size: 2rem;
  color: white; }

.home .container .section2, .home .container .section6 {
  height: 100vh;
  background-color: rgba(137, 2, 62, 0.3);
  display: grid;
  align-content: center;
  justify-content: left;
  padding-left: 50px;
  font-size: 2rem;
  color: white; }

.home .container .section3, .home .container .section7 {
  height: 100vh;
  background-color: rgba(115, 147, 126, 0.6);
  display: grid;
  align-content: center;
  justify-content: right;
  text-align: right;
  padding-right: 50px;
  font-size: 2rem;
  color: white; }

.home .container .section4, .home .container .section8 {
  height: 100vh;
  background-color: rgba(49, 24, 71, 0.4);
  display: grid;
  align-content: center;
  justify-content: left;
  padding-left: 50px;
  font-size: 2rem;
  color: white; }

.home .container .navitem {
  text-decoration: none;
  color: var(--color-primary); }

.login {
  height: 100vh;
  background-size: cover; }

.login .container {
  background: white;
  padding: 30px;
  padding-left: 50px;
  padding-right: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  box-shadow: inset 0 -1px 0 0 rgba(0, 0, 0, 0.2), 0 1px 5px rgba(0, 0, 0, 0.19); }

.login .container .form {
  width: 15rem;
  display: grid;
  justify-items: center; }

